import React, { useState, useEffect } from 'react';
import Game from './components/Game';
import { useAppStateValue } from './hooks/useAppState';
import { GameModeType, MenuStateType } from './hooks/useAppState';
import MainMenu from './components/mainmenu';
// import OptionsMenu from './components/OptionsMenu';
// import { PieceParticle } from './components/PieceParticle';
import './App.css';


export default function App() {
  const appState = useAppStateValue();
  const [isLoaded, setIsLoaded] = useState(false);

  // Simulate font loading
  useEffect(() => {
    const timer = setTimeout(() => {
      setIsLoaded(true);
    }, 500);
    return () => clearTimeout(timer);
  }, []);


  // Find if there's a game mode in the state stack
  const gameModeSearch = appState.containsGameMode();
  const gameMode = gameModeSearch ? gameModeSearch.current as GameModeType : undefined;

  if (!isLoaded) return null;

  return (
    <div style={{
      position: 'relative',
      width: '100vw',
      height: '100vh',
      display: 'flex',
      flexDirection:'column',
      justifyContent: 'center',
      alignItems: 'center',
      backgroundColor: 'rgba(0,0,0,0.8)',
      overflow: 'hidden'
    }}>
      {/* Background particles */}
      {/* {[...Array(15)].map((_, i) => (
        <PieceParticle key={`particle${i}`} />
      ))} */}
      
      <div id="play-area" style={{ 
        fontFamily: '"Press Start 2P", "Silkscreen", monospace',
        position: 'relative',
        userSelect: 'none',
        width: '100%',
        height: '100%',
        maxWidth: '100vw',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection:'column'
      }}>
        {/* Show MainMenu if we're at the menu and no game is active */}
        {(appState.containsState(MenuStateType.MENU) && !gameMode) && <MainMenu />}
        
        {/* Show Game if we have an active game mode */}
        {gameMode && <Game />}
        
        {/* Show Options menu if it's in the state stack */}
        {/* {appState.containsState(MenuStateType.OPTIONS) && <OptionsMenu />} */}
      </div>
    </div>
  );
}