import React, { useEffect, useState } from 'react';
import { useSetAppState, MenuStateType, GameModeType } from '../hooks/useAppState';
import { cssColors } from '../constants/Color';
import { PieceData } from '../constants/piece';



export default function MainMenu() {
  const [_, appendAppState] = useSetAppState();
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  return (
    <div className="menu-container">
      
      
      <h1 className={`menu-logo ${isVisible ? 'logo-animate' : ''}`}>
        Blast Pi
      </h1>

      <MainButton
        onClick={() => {
          appendAppState(GameModeType.Classic);
        }}
        backgroundColor={cssColors.brightNiceRed}
        title={"Classic ∞"}
        flavorText={"classical line breaking"}
        idleBounce={true}
      />
      
      <MainButton
        onClick={() => {
          appendAppState(MenuStateType.OPTIONS);
        }}
        backgroundColor={cssColors.green}
        title={"Options"}
      />

      <div className={`menu-footer ${isVisible ? 'fade-in' : ''}`}>
        beta version
      </div>
    </div>
  );
}

function MainButton({
  style,
  textStyle,
  backgroundColor,
  title,
  flavorText,
  idleBounce,
  onClick,
}: {
  style?: React.CSSProperties;
  textStyle?: React.CSSProperties;
  backgroundColor: string;
  title: string;
  flavorText?: string;
  idleBounce?: boolean;
  onClick?: () => void;
}) {
  const [isHovered, setIsHovered] = useState(false);
  const [isAnimating, setIsAnimating] = useState(false);

  const handleClick = () => {
    setIsAnimating(true);
    setTimeout(() => {
      setIsAnimating(false);
      if (onClick) onClick();
    }, 200);
  };

  const buttonStyle: React.CSSProperties = {
    backgroundColor,
    border: '2px solid rgba(0, 0, 0, 0.3)',
    transform: `translateY(${isHovered ? -10 : 0}px) ${isAnimating ? 'scale(1.1)' : ''} ${
      idleBounce ? 'translateY(-5px)' : ''
    }`,
    transition: 'transform 0.3s ease, box-shadow 0.3s ease',
    boxShadow: isHovered 
      ? '0 10px 20px rgba(0, 0, 0, 0.2)' 
      : '0 4px 8px rgba(0, 0, 0, 0.1)',
    ...style
  };

  return (
    <div 
      className="button-pressable"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      onClick={handleClick}
    >
      <div className="button" style={buttonStyle}>
        <div className="button-text" style={textStyle}>
          {title}
        </div>
        {flavorText && (
          <div className="button-flavor-text" style={textStyle}>
            {flavorText}
          </div>
        )}
      </div>
    </div>
  );
}