import React, {useEffect} from 'react';
import { GRID_BLOCK_SIZE } from './Game';

type CellState = {
  filled: boolean;
  color?: string;
  style?: React.CSSProperties;
};

interface GridProps {
  gridRef: React.RefObject<HTMLDivElement>;
  grid: CellState[][];
  isAnimating: boolean;
  createFilledBlockStyle: (color: { r: number; g: number; b: number }, borderWidth?: number) => any;
  parseRgbString: (rgbString: string) => { r: number; g: number; b: number };
  activeBlockColor: { r: number; g: number; b: number } | null;
}

export default function Grid({ 
  gridRef, 
  grid, 
  isAnimating, 
  createFilledBlockStyle, 
  parseRgbString,
  activeBlockColor
}: GridProps) {
  useEffect(() => {
    console.log('Active block color changed:', activeBlockColor);
  }, [activeBlockColor]);
  
  const renderGridCell = (cell: CellState, index: number) => {
    const cellStyle = cell.filled ? 
      createFilledBlockStyle(
        typeof cell.color === 'string' ? 
          parseRgbString(cell.color) : 
          { r: 100, g: 100, b: 100 }
      ) : 
      {
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        border: '1px solid #ddd',
        boxSizing: 'border-box' as const
      };

    return (
      <div
        key={index}
        className="grid-cell"
        style={{
          width: `${GRID_BLOCK_SIZE}px`,
          height: `${GRID_BLOCK_SIZE}px`,
          ...cellStyle,        
        }}
      />
    );
  };

  // Determine border color based on active block
  const getBorderColor = () => {
    if (activeBlockColor) {
      return `rgb(${activeBlockColor.r}, ${activeBlockColor.g}, ${activeBlockColor.b})`;
    }
    return 'white'; // Default border color
  };

  return (
    <div 
      ref={gridRef}
      style={{
        width: `${GRID_BLOCK_SIZE * 8}px`,
        height: `${GRID_BLOCK_SIZE * 8}px`,
        border: `2px solid`,
        borderColor: activeBlockColor ? 
        `rgb(${activeBlockColor.r}, ${activeBlockColor.g}, ${activeBlockColor.b})` : 
        'white',
        display: 'grid',
        gridTemplateColumns: `repeat(8, ${GRID_BLOCK_SIZE}px)`,
        gridTemplateRows: `repeat(8, ${GRID_BLOCK_SIZE}px)`,
        backgroundColor: 'rgba(0, 0, 0, 0.8)',
        pointerEvents: isAnimating ? 'none' : 'auto',
      }}
    >
      {grid.flat().map((cell, i) => renderGridCell(cell, i))}
    </div>
  );
}