export default function StylizedButton({text, onClick, background, centered, borderColor}: {text: string, onClick?: () => void, background: string, centered?: boolean, borderColor?: string}) {
    if (centered == undefined) {
        centered = true;
    }
    return <button onClick={onClick} style={{width: 160,
		height: 30,
		borderRadius: 10,
        display:'flex',
		justifyContent: 'center',
		alignItems: 'center',
		margin: 4,
        backgroundColor:background, 
        alignSelf: 
            centered ? 'center' : 'flex-start', 
        borderWidth: 2, 
        borderColor: borderColor ? borderColor : "transparent"
        }}>
        <p style={{fontSize: 18,
		color: 'white',
		fontFamily: 'Silkscreen Bold',
        fontWeight: '100'}}>{text}</p>
    </button>
}
