import React, { useState, forwardRef, useImperativeHandle } from 'react';

const bomb = require("../assets/images/bomb.png")
const demolish = require("../assets/images/bomb.png")
const double = require("../assets/images/double.png")

const one = require("../assets/images/one.png")
const zero = require("../assets/images/zero.png")

// Define types
export type PowerUpType = "clearBoard" | "doublePoints" | "bomb";

type PowerUpProps = {
  onClick: Array<() => void>;
  refillHandler: (type: PowerUpType) => void;
};

export type PowerUpRef = {
  incrementPowerUp: (type: PowerUpType) => void;
    resetPowerUps: () => void; // Add this line

};

// StylizedPowerUp sub-component
interface StylizedPowerUpProps {
  backgroundColor: string;
  text: string;
  counter: number;
  onClick: () => void;
  image: string
}

const StylizedPowerUp: React.FC<StylizedPowerUpProps> = ({
  backgroundColor,
  text,
  counter,
  onClick,
  image
}) => {
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        margin: '0 10px',
        cursor: 'pointer',
        opacity: counter > 0 ? 1 : 0.6,
        position: 'relative' // Added to position the counter badge absolutely
      }}
      onClick={onClick}
    >
      {/* Counter badge positioned in top-right corner */}
      <img 
        src={counter ? one : zero} 
        style={{
          width: '25px',
          height: '25px',
          position: 'absolute',
          top: '-8px',    // Positioned at the top
          right: '-8px',  // Positioned at the right
          zIndex: 10      // Ensure it appears above other elements
        }}
        alt="counter"
      />
      
      <img 
        style={{
          width: '50px', 
          height: '50px',
          marginBottom: '4px'
        }} 
        src={image} 
        alt={text} 
      />  
      
      <div
        style={{
          fontSize: '14px',
          fontWeight: 'bold',
          color: 'white',
          marginBottom: '4px',
        }}
      >
        {text}
      </div>
    </div>
  );
};

// Main PowerUp component
const PowerUp = forwardRef<PowerUpRef, PowerUpProps>(
({ onClick,   refillHandler }, ref) => {
    const [powerUps, setPowerUps] = useState<Record<PowerUpType, number>>({
      clearBoard: 1,
      doublePoints: 1,
      bomb: 1,
    });

    function handlePowerUp(type: PowerUpType, index: number) {
      if (powerUps[type] > 0) {
        onClick[index]();
        setPowerUps((prev) => ({ ...prev, [type]: prev[type] - 1 }));
      } else {
        refillHandler(type);
      }
    }

    // Expose method to parent
    // Expose method to parent
useImperativeHandle(ref, () => ({
  incrementPowerUp(type: PowerUpType) {
    setPowerUps((prev) => ({ ...prev, [type]: prev[type] + 1 }));
  },
  resetPowerUps() { // Add this method
    setPowerUps({
      clearBoard: 1,
      doublePoints: 1,
      bomb: 1,
    });
  },
}));

    return (
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-around",
          padding: '15px',
          borderRadius: '15px',
          margin: '20px 0',
          backgroundColor: 'rgba(0, 0, 0, 0.3)',
        }}
      >
        <StylizedPowerUp
          backgroundColor="#2c3e50"
          text="Demolish"
          counter={powerUps["clearBoard"]}
          onClick={() => {
            handlePowerUp("clearBoard", 0);
          }}
          image={bomb}
        />
        <StylizedPowerUp
          backgroundColor="#e74c3c"
          text="Bomb"
          counter={powerUps["bomb"]}
          onClick={() => {
            handlePowerUp("bomb", 1);
          }}
          image={bomb}
        />
        <StylizedPowerUp
          backgroundColor="#f39c12"
          text="Double"
          counter={powerUps["doublePoints"]}
          onClick={() => {
            handlePowerUp("doublePoints", 2);
          }}
          image={double}
        />
      </div>
    );
  }
);

export default PowerUp;