import { PieceData, getRandomPiece } from "./piece";

export type Hand = (PieceData | null)[];

export function createRandomHand(size: number): Hand {
    const hand = new Array<PieceData>(size);
    for (let i = 0; i < size; i++) {
        hand[i] = getRandomPiece();
    }
    return hand;
}