import React from 'react';
import { PieceData } from '../constants/piece';
import { HAND_BLOCK_SIZE } from './Game';

interface block {
  id: string;
  piece: PieceData | null;
  ref: React.RefObject<HTMLDivElement | null>;
}

type BlockType = {
  matrix: number[][];
  color: string;
  width: number;
  height: number;
  ref: React.RefObject<HTMLDivElement | null>;
};

interface HandProps {
  blocks: block[];
  createFilledBlockStyle: (color: { r: number; g: number; b: number }, borderWidth?: number) => any;
  parseRgbString: (rgbString: string) => { r: number; g: number; b: number };
}

export default function Hand({ 
  blocks, 
  createFilledBlockStyle, 
  parseRgbString 
}: HandProps) {
  
  const renderBlock = (block: BlockType, index: number) => {
    const blockColor = parseRgbString(block.color);
    
    return (
      <div
        key={`block-${index}`}
        ref={block.ref}
        style={{
          width: `${block.width * HAND_BLOCK_SIZE}px`,
          height: `${block.height * HAND_BLOCK_SIZE}px`,
          cursor: 'move',
          zIndex: 10,
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          position: 'relative'
        }}
      >
        {block.matrix.map((row, y) => (
          row.map((cell, x) => (
            cell === 1 ? (
              <div
                key={`${y}-${x}`}
                style={{
                  position: 'absolute',
                  width: `${HAND_BLOCK_SIZE}px`,
                  height: `${HAND_BLOCK_SIZE}px`,
                  ...createFilledBlockStyle(blockColor, 2),
                  left: `${x * HAND_BLOCK_SIZE}px`,
                  top: `${y * HAND_BLOCK_SIZE}px`,
                  boxSizing: 'border-box'
                }}
              />
            ) : null
          ))
        ))}
      </div>
    );
  };

  return (
    <div style={{ 
      display: 'flex',
      justifyContent: 'space-around',
      alignItems: 'center',
      width: '100%',
      marginBottom: '30px',
      height: '100px',
    }}>
      {blocks.map((block, index) => {
        // If piece is null, render empty placeholder to maintain positioning
        if (!block.piece) {
          return (
            <div
              key={block.id}
              style={{
                width: `${HAND_BLOCK_SIZE * 3}px`, // Default width
                height: `${HAND_BLOCK_SIZE * 3}px`, // Default height
                visibility: 'hidden', // Hidden but takes up space
              }}
            />
          );
        }
        
        // Render normal block
        return renderBlock({
          matrix: block.piece.matrix,
          color: `rgb(${block.piece.color.r}, ${block.piece.color.g}, ${block.piece.color.b})`,
          width: block.piece.width,
          height: block.piece.height,
          ref: block.ref
        }, index);
      })}
    </div>
  );
}